@import "styles/color.scss";

.root {
  align-items: center;
  background-color: $color-22;
  min-height: 60px;
  padding: 0 20px;

  .left-container {
    flex: 1;

    img {
      max-height: 30px;
    }
  }

  .right-container {
    display: flex;

    .username {
      text-align: center;
      font-weight: bold;
      padding: 10pt 0;
    }

    .avatar {
      margin-left: 20px;
    }
  }
}
