@import "styles/color.scss";

.root {
  position: fixed;
  bottom: 0;
  right: 0;
  margin-right: 30px;

  p {
    font-size: 12px;
    margin: 0;
    display: flex;

    span {
      font-weight: bold;
      display: block;
      min-width: 60px;
      text-align: right;
      padding-right: 5px;
    }

    &.fetch {
      color: blue;

      &.success {
        color: green;
      }
    }
  }
}
