@import 'styles/color.scss';

.root {
  display: flex;
  width: 100%;
  flex-direction: column;

  .label {
    padding-bottom: 5px;
    // margin-right: 5px;
  }

  .wrapper {
    display: flex;
    flex-direction: column;

    .read-only-value {
      font-weight: unset;
      padding: 5px;
      min-height: 31px;
    }

    .text-field {
      flex: 1;
      &.no-margin {
        margin-bottom: 0px;
      }
      margin-bottom: 23px;
      display: flex;

      .form-control {
        width: 100%;
      }

      .input {
        width: 100%;
        * {
          padding: 10px;
        }
      }

      .input-amount {
        width: 100%;
        * {
          padding: 10px;
          text-align: right;
        }
      }

      .end-adornment {
        color: #8b8b8b;
        font-weight: 500;
        padding: 10px;
      }

      .select {
        color: #000000;
        padding: 4px;
        padding-left: 10px;
      }

      border: 1px solid transparent;

      &.focus {
        border-color: green;
      }

      // Errors
      &.has-error {
        border-color: red;
        margin-bottom: unset;
      }
    }

    span.error {
      padding-top: 1px;
      color: red;
    }
  }
}
