@import "styles/color.scss";

.root {
  position: fixed;
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  z-index: 9999;

  .overlay {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: black;
    z-index: 9998;
    opacity: 0.4;
  }

  .loading {
    position: absolute;
    z-index: 9998;
    top: 150px;
  }
}
