.app-bar {
  position: relative;
}

.root {
  position: relative;
  background: #525659;
  min-height: 100%;
  max-height: 100%;
  display: flex;
  flex-direction: column;

  .controls {
    display: flex;
    align-items: center;
    padding: 10px;
    background-color: #323639;
    top: 0;

    .file-name {
      flex: 1;
    }

    .search {
      margin-right: 5px;
    }

    .control {
      span {
        color: white;
        font-size: 17px;
      }

      &.disabled {
        span {
          color: #636363;
        }
      }

      .text-field {
        padding: 5px;
        margin-right: 5px;
      }
    }
  }

  .viewport {
    overflow: auto;
    flex: 1;
    display: flex;
    max-width: 100%;
    .pdf {
      margin: auto;

      .page {
        margin-top: 10px;
      }
    }
  }

  .page-number {
    color: #dddddd;
    font-weight: 600;
  }

  .file-name {
    color: #dddddd;
    font-weight: 600;
    margin-right: 50px;
  }

  .iframe {
    flex: 1;
  }
}
