@import "styles/color.scss";

.main-container {
  display: flex;
  overflow: hidden;
  height: 100%;
  min-width: 20%;

  .sub-container {
    flex: 1;
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 95%;

    .content {
      display: flex;
      flex-direction: column;
      flex: 1;
      padding: 20px;
      overflow: auto;
      background-color: $color-9;
      position: relative;
    }
  }
}
