@import "styles/color.scss";

.root {
  background-color: $color-1;
  min-width: 80px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 20px;

  .menu {
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow: hidden;

    .item {
      display: flex;
      flex-direction: column;
      align-items: center;
      color: $color-24;
      font-size: 10px;
      font-weight: bold;
      margin: 0 auto 20px auto;
      cursor: pointer;
      text-decoration: none;

      .circle {
        background-color: $color-23;
        min-width: 45px;
        min-height: 45px;
        max-width: 45px;
        max-height: 45px;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;

        .icon {
          font-size: 18px;
        }
      }

      .text {
        text-align: center;
      }
    }

    .item:nth-child(6) {
      margin-bottom: 60px;
    }

    .item:last-child {
      margin: 30px;
    }

    button.item {
      span:first-child {
        display: flex;
        flex-flow: column;
      }
    }
  }
}
