@import 'styles/color.scss';

.main-contaner,
.wrapper {
  height: 100%;
}

.table {
  width: 100%;
  overflow-x: auto;
}

.table-loading,
.table-no-data {
  align-items: center;
  justify-content: center;
  height: 80px;
}

.text-link {
  color: $color-4;
  cursor: pointer;
  text-decoration: none;
}

.underline {
  text-decoration: underline;
}

.table-title {
  font-size: 1.08rem !important;
}

.table-initial-state {
  padding: 20px;
  text-align: center;
}

.bold {
  font-weight: bold !important;
}

.box-shadow-field {
  box-shadow: 0.5px 0.5px 1px 0.8px rgba(0, 0, 0, 0.4) !important;
}

