}

.color-negative {
  color: #f50057;
}

.label,
.label-2 {
  font-weight: bold !important;
  color: $color-11;
}

.content-text,
.content-text-2 {
  font-weight: unset !important;
  color: $color-12;
}

.label-2,
.content-text-2 {
  font-size: 1rem !important;
}

.button-positive {
  background-color: $color-13 !important;

  &:hover {
    background-color: $color-14 !important;
  }
}

.button-neutral {
  background-color: $color-15 !important;

  &:hover {
    background-color: $color-16 !important;
  }
}

.button-negative {
  background-color: $color-17 !important;

  &:hover {
    background-color: $color-18 !important;
  }
}

.mr-8 {
  margin-right: 8px !important;
}
