.container {
  padding: 20px;
  margin-top: 100px;
  margin-bottom: 50px;
  min-width: 350px;

  .error {
    color: red;
    margin: 10px 0;
    display: block;
    min-height: 20px;

    &.hidden {
      visibility: hidden;
    }
  }
  .form {
    display: flex;
    flex-direction: column;

    .forgot-password {
      text-align: end;
      margin: 0 0;
      font-size: 10px;
      position: relative;
      top: -115px;
    }
  }
}
.logo {
  text-align: right;
  img {
    padding-top: 20px;
    max-height: 70px;
  }
}
.note {
  font-size: 10px;
}
