.root {
  display: flex;
  flex-direction: column;
  background-color: #dddfe3;
  padding: 10px;
  padding-top: unset;

  .header {
    display: flex;
    padding: 15px 0;
  }

  .items {
    background-color: white;

    .item {
      padding: 10px;
      border-bottom: 1px solid #d0d0d0;

      &:nth-child(odd) {
        background-color: #f6f7fa;
      }

      .item-header {
        display: flex;

        .item-name {
          flex: 1;
          display: flex;
          align-items: center;
        }
      }

      .files {
        display: flex;
        padding-top: 10px;

        .file {
          width: 120px;
          margin-right: 10px;

          &:last-child {
            margin-right: unset;
          }

          .preview {
            height: 120px;
            width: 120px;
            border: 1px solid #d4d4d4;
            border-radius: 5px;
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 30px;
            color: green;
            cursor: pointer;
            position: relative;
            overflow: hidden;

            img {
              width: 100%;
            }

            .remove {
              position: absolute;
              top: 0;
              right: 0;
              color: red;
              cursor: pointer;
              font-size: 21px;

              &:hover {
                transform: scale(1.1);
              }
            }
          }

          font-size: 12px;

          .file-name {
            display: block;
            margin-right: 0;
            margin-bottom: 3px;
            width: auto;
            text-overflow: ellipsis;
            white-space: nowrap;
            overflow-x: hidden;
            padding-top: 5px;
          }

          .file-size {
            color: #8a8a8a;
          }
        }
      }
    }
  }
}
